@import "../../styles/variables.scss";

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid $neutralColor400;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  margin-top: 100px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
